import React, { useState } from 'react'
import Preloader from '../../common/Preloader/Preloader'
import ProfileData from './ProfileData'
import ProfileDataForm from './ProfileDataForm'
import s from './ProfileInfo.module.css'
import ProfileStatusWithHooks from './ProfileStatusWithHooks'
import choosePhoto from '../../../assets/images/choosePhoto.png'

const ProfileInfo = ({
	profile,
	status,
	updateStatus,
	isOwner,
	savePhoto,
	userId,
	saveUserInfo,
}) => {
	let [editMode, setEditMode] = useState(false)
	if (!profile) {
		return <Preloader />
	}
	function mainPhotoSelected(e) {
		if (e.target.files.length) savePhoto(e.target.files[0])
	}
	function onSubmit(formData) {
		saveUserInfo(formData).then(() => {
			setEditMode(false)
		})
	}

	return (
		<div className={s.descriptionBlock}>
			<div>
				<img
					alt='profile'
					src={profile.photos.large}
					className={s.profilePic}
				/>
				{isOwner ? (
					<label for='inputTag' className={s.changeProfilePicBtn}>
						<input
							type='file'
							onChange={mainPhotoSelected}
							className={s.changeProfilePicBtn}
							id='inputTag'
						/>
						<p>Choose a photo</p>
						<img src={choosePhoto} />
					</label>
				) : null}
				<ProfileStatusWithHooks
					status={status}
					updateStatus={updateStatus}
				/>
			</div>
			<div className={s.horisontalLine}></div>
			{!editMode ? (
				<ProfileData
					toggleEditMode={() => setEditMode(true)}
					profile={profile}
					isOwner={isOwner}
					editMode={editMode}
					contacts={profile.contacts}
				/>
			) : (
				<ProfileDataForm
					initialValues={profile}
					onSubmit={onSubmit}
					editMode={editMode}
					contacts={profile.contacts}
				/>
			)}
		</div>
	)
}

export default ProfileInfo
